/**
 * Font
 */
/**
 * Body Text Font Sizes
 */
/**
 * Add font scale with media query as per configuration on $body-text
 * Ex: @include body-text( h1 );
 * This will add font-size and line-height with different breakpoints.
 */
a {
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

/**
* Box-sizing
*/
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Manuale", sans-serif;
  font-size: 15px;
  line-height: 1.5em;
  background: var(--wp--preset--color--background-body);
  color: var(--wp--preset--color--cyan-cornflower-blue);
  font-weight: 400;
  overflow-anchor: none;
  /* fixes "load more" content in Chrome */
}
@media (min-width: 75em) {
  body {
    font-size: 19px;
  }
}

a {
  cursor: pointer;
  color: var(--wp--preset--color--link);
  text-decoration: none;
}
a:focus {
  outline: 5px auto -webkit-focus-ring-color;
  outline-offset: -2px;
}
a:hover,
a a:active {
  color: var(--wp--preset--color--link-hover);
  text-decoration: none;
  outline: 0;
}

/**
 * HEADINGS
 */
/* stylelint-disable */
h1, .h1 {
  font-size: 33px;
  line-height: 0.9em;
}
@media (min-width: 48em) {
  h1, .h1 {
    font-size: 60px;
  }
}
@media (min-width: 75em) {
  h1, .h1 {
    font-size: 80px;
  }
}

h2, .h2 {
  font-size: 30px;
  line-height: 0.9em;
}
@media (min-width: 48em) {
  h2, .h2 {
    font-size: 50px;
  }
}
@media (min-width: 75em) {
  h2, .h2 {
    font-size: 60px;
  }
}

h3, .h3 {
  font-size: 26px;
  line-height: 1em;
}
@media (min-width: 48em) {
  h3, .h3 {
    font-size: 35px;
  }
}
@media (min-width: 75em) {
  h3, .h3 {
    font-size: 42px;
  }
}

h4, .h4 {
  font-size: 20px;
  line-height: 1.2em;
}
@media (min-width: 48em) {
  h4, .h4 {
    font-size: 21px;
  }
}
@media (min-width: 75em) {
  h4, .h4 {
    font-size: 26px;
  }
}

h5, .h5 {
  font-size: 18px;
  line-height: 1.6em;
}
@media (min-width: 75em) {
  h5, .h5 {
    font-size: 22px;
  }
}

h6, .h6 {
  font-size: 15px;
  line-height: 1.5em;
}
@media (min-width: 48em) {
  h6, .h6 {
    font-size: 20px;
  }
}
@media (min-width: 75em) {
  h6, .h6 {
    font-size: 19px;
  }
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  font-family: "Oswald", "sans-serif";
  font-weight: 500;
  margin-top: 0.7625em;
  margin-bottom: 20px;
  text-transform: uppercase;
  text-rendering: optimizelegibility;
}

p.large {
  font-size: 18px;
  line-height: 1.6em;
}
@media (min-width: 48em) {
  p.large {
    font-size: 20px;
  }
}
@media (min-width: 75em) {
  p.large {
    font-size: 24px;
  }
}

/**/
.content h1:first-child,
.content h2:first-child,
.content h3:first-child,
.content h4:first-child,
.content h5:first-child,
.content h6:first-child,
.content p:first-child,
.content ul:first-child {
  margin-top: 0;
}

/**
 * Lists
 */
ul,
ol {
  margin-top: 20px;
  margin-bottom: 27px;
  list-style-type: none;
}

ol,
ul {
  overflow-wrap: break-word;
}

ul,
ol,
li ul,
li ol {
  margin-left: 35px;
}

li ul,
li ol {
  margin: 0;
  padding: 0;
}

p {
  margin-top: 20px;
  margin-bottom: 27px;
}

li {
  padding-left: 21px;
  position: relative;
}

ul > li:before,
ol > li:before {
  position: absolute;
  left: 0;
}

ul > li:before {
  content: "";
  width: 8px;
  height: 8px;
  background-color: var(--wp--preset--color--quaternary);
  display: inline-block;
  top: 9px;
}

body,
ol {
  counter-reset: counter;
}

ol > li:before {
  counter-increment: counter;
  content: counter(counter) ".";
}

li {
  margin-top: 7px;
}

/**
 * Form Fields
 */
/* Removes default webkit form styling */
input:not([type=radio]):not([type=checkbox]),
button,
textarea {
  -webkit-appearance: none;
}

input,
label,
textarea {
  font-family: "Montserrat", "sans-serif";
  font-size: 14px;
  display: block;
}

input,
textarea {
  border: 1px solid var(--wp--preset--color--light-periwinkle);
  background-color: var(--wp--preset--color--white);
  color: var(--wp--preset--color--black);
  width: 100%;
  font-weight: 500;
  outline: 0;
}

/* default text input style */
@media (min-width: 37.5em) {
  input[type=text],
input[type=date],
input[type=datetime],
input[type=datetime-local],
input[type=email],
input[type=month],
input[type=number],
input[type=password],
input[type=search],
input[type=tel],
input[type=url],
input[type=week],
input[type=date],
.iti__flag-container {
    padding: 0 16px;
    height: 45px;
    border-radius: 0;
  }
}
@media (min-width: 75em) {
  input[type=text],
input[type=date],
input[type=datetime],
input[type=datetime-local],
input[type=email],
input[type=month],
input[type=number],
input[type=password],
input[type=search],
input[type=tel],
input[type=url],
input[type=week],
input[type=date],
.iti__flag-container {
    height: 54px;
    line-height: 54px;
  }
}

label {
  color: var(--wp--preset--color--arsenic);
  text-transform: uppercase;
  font-weight: 600;
  line-height: 1em;
  text-align: left;
  padding: 0 0 5px;
}

label[class=error] {
  padding: 6px 0;
  color: var(--wp--preset--color--red);
  font-weight: 400;
  font-size: 12px;
  text-transform: none;
}

label.hidden-label {
  color: transparent;
}

textarea {
  padding: 16px;
  height: 160px;
}
select {
  outline: 0;
}

button {
  font: inherit;
  cursor: pointer;
}

input[type=checkbox],
input[type=radio] {
  cursor: pointer;
  font: inherit;
  display: grid;
  appearance: none;
  -webkit-appearance: none;
  background-color: var(--wp--preset--color--white);
  color: currentColor;
  width: 1.3em;
  height: 1.3em;
  border-radius: 0.15em;
  /* transform: translateY(0.15em); */
  margin: 0;
  margin-right: 0.2em;
  place-content: center;
}

input[type=checkbox]:before {
  content: "";
  width: 0.7em;
  height: 0.7em;
  transform: scale(0);
  transition: 120ms transform ease-in-out;
  box-shadow: inset 1em 1em var(--wp--preset--color--granite-gray);
}
input[type=checkbox]:checked:before {
  transform: scale(1);
}
input[type=checkbox].disabled {
  cursor: default;
}

/**
 * Separator
 */
hr {
  border-bottom: 1px solid var(--wp--preset--color--light-periwinkle);
}

/**
 * Text Styling
 */
.montserrattlt {
  font-family: "Montserrat", "sans-serif";
  font-weight: 600;
  color: var(--wp--preset--color--foreground);
  text-transform: none;
}

.text-shadow-black {
  text-shadow: 1px 1px 3px var(--wp--preset--color--black);
}

/**
* Buttons
*/
[class*=btn-] {
  position: relative;
  border: none;
}
[class*=btn-]:before {
  margin-right: 5px;
}
[class*=btn-]:after {
  padding-left: 5px;
  content: "\e913";
}
[class*=btn-]:hover {
  color: var(--wp--preset--color--white);
}
@media (max-width: 47.99em) {
  [class*=btn-] {
    font-size: 12px;
    padding: 15px 20px;
  }
}
@media (max-width: 74.99em) {
  [class*=btn-] {
    padding: 12px 12px;
  }
}

.btn-primary, .btn-secondary, .btn-accent, .btn-tertiary, .btn-quaternary {
  font-family: "Montserrat", "sans-serif";
  font-weight: 600;
  font-size: 14px;
  color: var(--wp--preset--color--white);
  text-transform: uppercase;
  text-align: center;
  display: inline-block;
  padding: 14px 20px;
  height: 100%;
}
.btn-block {
  display: block;
  width: 100%;
}
.btn-small {
  padding: 4px 30px;
}
.btn-small:after {
  display: none;
}
.btn-no-arrow:after {
  display: none;
}
.btn-primary {
  background-color: var(--wp--preset--color--primary);
}
.btn-primary:hover {
  background-color: var(--wp--preset--color--primary-hover);
}
.btn-secondary {
  background-color: var(--wp--preset--color--secondary);
}
.btn-secondary:hover {
  background-color: var(--wp--preset--color--secondary-hover);
}
.btn-tertiary {
  border: 2px solid var(--wp--preset--color--primary);
}
.btn-tertiary:hover {
  background-color: var(--wp--preset--color--primary-hover);
}
.btn-quaternary {
  background-color: var(--wp--preset--color--quaternary);
}
.btn-quaternary:hover {
  background-color: var(--wp--preset--color--quaternary-hover);
}

/*Book Button*/
@media (min-width: 48em) {
  .book-btn {
    position: absolute;
    top: 0px;
    right: 0px;
    width: 230px;
    height: 100%;
    background-color: var(--wp--preset--color--primary);
  }
  .book-btn a {
    font-size: 13px;
    right: auto;
    top: 1px;
    line-height: 3em !important;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
  }
  .book-btn a:after {
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
  .book-btn a:hover:after {
    transform: translateX(25px);
  }
}
.accrdn h4 {
  display: inline-block;
  margin-top: 0.2em;
}
.accrdn .accrdn [class*=icon-] {
  color: var(--wp--preset--color--secondary);
  top: 0;
}
.accrdn span:before {
  font-weight: 300;
  font-size: 25px;
  position: absolute;
  left: 0;
}
.accrdn span:before {
  color: var(--wp--preset--color--secondary);
  top: 0;
}

.mobile-accrdn h5 {
  display: inline-block;
  margin-top: 0.2em;
  margin-left: 70px;
}
.mobile-accrdn span:before {
  font-weight: 300;
  font-size: 25px;
  position: absolute;
  left: 0;
}
.mobile-accrdn span:before {
  color: var(--wp--preset--color--quaternary);
  font-size: 50px;
  line-height: 50px;
  top: 0;
}

.banner-overlay {
  position: absolute;
  margin-top: -800px;
  left: 0;
  width: 100%;
  height: 800px;
  text-align: center;
  color: var(--wp--preset--color--white);
}